<template>
  <div class="subPage">
    <div class="pageTitle">垃圾车定位</div>
    <div>
      <el-date-picker v-model="dates" type="date" placeholder="选择日期" value-format="yyyy-MM-dd">
      </el-date-picker>
      <el-select v-model="value" placeholder="请选择人员" style="margin-left: 20px" @change="change">
        <el-option v-for="(item,index) in person" :key="index" :id="item.id" :value="item.xm">
        </el-option>
      </el-select>
      <el-button class="btn_search" type="success" round @click="search">搜索</el-button>
    </div>
    <div class="contents">
      <span class="w25" v-for="(item,index) in tableData" :key="index">{{item.sj}} <p>{{itme.dep}}</p></span>
    </div>
  </div>
</template>
<script>
import {_Widget} from "../../common";
import {Downloader} from "../../common";
import {commonConfig} from "../../common";

export default {
  data() {
    return {
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      dates: '',
      tableData:[],
      person:[],
      value: '',
      id:''
    };
  },
  methods: {
    change($event){
      if(this.person.length){
        for(var i = 0; i < this.person.length; i++){
          if(this.person[i].xm == $event){
            this.id = this.person[i].id
          }
        }
      }
    },
    search(){
      var thisObject = this;
      if(!thisObject.id){
        thisObject.$message({
          type: 'error',
          message: '请选择人员!'
        });
        return ;
      }
      var data = {
        type:'clgj',
        date:thisObject.dates,
        personId:thisObject.id,
      };
      var url = commonConfig.baseURL + "of_yf_cx";
      Downloader.loadXMLAsync(url + "?as_json=" + JSON.stringify(data), function(e) {
        var xmldoc = e.response;
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          thisObject.tableData = res.data;
        }
      },function () {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
      })
    },
    getPerson(){
      var thisObject = this;
      var data = {
        type:'person'
      };
      var url = commonConfig.baseURL + "of_yf_cx";
      Downloader.loadXMLAsync(url + "?as_json=" + JSON.stringify(data), function(e) {
        var xmldoc = e.response;
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          res.data.sort(function(a,b){
            return a.xm.localeCompare(b.xm,"zh")
          })
          thisObject.person = res.data;
        }else{
          thisObject.$message({
            type: 'error',
            message: '未查询到相关数据!'
          });
        }
      },function () {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
      })
    }
  },
  created() {
    this.getPerson();
  }

}
</script>

<style lang="scss" scoped>
@import '~@/assets/scss/subPage';
</style>
